@media only screen and (min-width : 320px) {
  .start_page_game_title{
    margin: 0;
    margin-bottom: 50px;
    padding-top: 140px;
    font-size: 45px;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.67);
    color: white;
    text-align: center;
  }
  .start_page_btn{
    border: none;
    background-color: white;
    border-radius: 82px;
    color: #c77b9f;
    -webkit-box-shadow: 0 4px 7px rgba(0, 0, 0, 0.43);
            box-shadow: 0 4px 7px rgba(0, 0, 0, 0.43);
    font-family: 'Delius Unicase', cursive;
  }
  .start_page_play_btn{
    font-size: 40px;
    padding: 12px 44px;
  }

  .score_n_screator_container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-flow: row;
        flex-flow: row;
    padding-top: 50px;
  }
  .start_page_btn_secondary{
    font-size: 20px;color: #d1466f;
    padding: 12px 24px;
  }
}



